@import './assets/styles/_fonts.scss';

$bg-red: #CC2840;

@mixin center-content {
    display: flex;
    align-items: center;
    justify-content: center;
}

* {
    font-family: $Outfit  !important;
}

::selection {
    color: white;
    background: $red;
}

h1,
h2 {
    @extend %title;
}

h3 {
    font-size: 4vw;
    color: $red;
}


//NOT YET DONE
a {
    color: #141414;
}

.loading-container {
    position: fixed;
    width: 100%;
    height: 100%;
    top: 0;
    left: 0;
    background: #fff;

    .loading {
        position: absolute;
        top: 50%;
        left: 50%;
        z-index: 10;
        margin-top: -105px;
        margin-left: -105px;
    }
}

.wrapper {
    margin: 0 auto;
    max-width: 3440px;
    // overflow-x: hidden;
}

.container {
    max-width: 1860px;
    padding: 0 30px;
    margin: 0 auto;

}


//home page scss//
.home {

    .img-container {
        overflow: hidden;
        padding: 0;

        img {
            width: 100%;
            height: 100%;
            object-fit: cover;
        }
    }

    .hero {
        width: 100%;
        height: 100vh;
        margin: 0 !important;
        // padding-top: 70px;
        align-items: end;
        overflow: hidden;
        position: relative;

        .img-container {
            height: 100%;
            width: 66.66666667%;
            margin-left: auto;

        }

        h1 {
            position: absolute;
            font-weight: 700;
            font-size: 134px;
            text-transform: uppercase;
            left: 8.33333333%;
            top: calc(70px + 5%);
        }

        p {
            position: absolute;
            bottom: 20%;
            left: 8.33333333%;
            max-width: 650px;
            line-height: 1.5em;
            font-size: 24px;
        }



    }

    a {
        color: darkgray;
    }

    h2 {
        font-size: 115px;
        font-weight: 500;
    }

    span {
        color: black;
    }

    .story {
        padding-top: 5rem;
        width: 100%;
        text-align: center;

        div p {
            font-size: 24px;
            line-height: 1.5em;
            margin-top: 50px;

            svg {
                width: 24px;
                vertical-align: middle;

            }
        }
    }

    .coffee {
        text-align: center;
        padding: 5rem 0;
        height: 85vh;
        width: 100%;
        position: relative;
        display: flex;

        .pattern {
            position: absolute;
            background-image: radial-gradient(#CC2840 0.5vw, transparent 0.5vw);
            height: 20vw;
            background-size: 3vw 3vw;

            &:first-child {
                width: 480px;
                bottom: 5%;
                left: 0;
            }

            &:nth-child(2) {
                width: 624px;
                top: 50%;
                right: 0;
                transform: translateY(-50%);
            }
        }

        .img-container {
            // display: flex;
            z-index: 1;
            position: relative;
            width: 25%;

            &:first-child {
                margin-right: auto;
            }

            &:last-child {
                margin-left: auto;
            }

            img {
                object-fit: cover;
                position: absolute;
                left: 50%;
                transform: translateX(-50%);
                height: 100%;
                transition: 0.2s ease-out;
                // width: 100%;
                // height: auto;
                // width: auto;
            }

        }
    }


    .awards {
        margin: 10rem 0;
        padding: 0 10%;
        height: 80vh;

        .col-8 {
            padding-right: 10%;
            text-align: right;
            display: flex;
            flex-direction: column;
            justify-content: space-between;
        }

        h2 {
            font-weight: 400;
            color: black;

            span {
                color: $bg-red;
            }
        }

        p {
            font-size: 24px;
        }
    }

    .ig {
        margin: 10rem 0;

        h2 {
            text-align: center;
            font-weight: 400;
            text-transform: uppercase;
        }

        .feeds-slide {
            padding-left: 10%;
            display: flex;
            justify-content: space-between;
            margin: 5rem 0;

            .img-container {
                margin-top: 0.5rem;
                width: 280px;
                height: 280px;
            }
        }

        .links {
            padding-left: 10%;
            display: flex;
            align-items: center;

            p {
                display: inline;
                font-size: 24px;
                margin: 0 1rem 0 0;
            }

            svg {
                margin-right: 1rem;
                display: inline;
                height: 24px;
            }
        }

    }

    // fade-in function

    .main_content {
        .fade-in {
            opacity: 0;
        }
    }
}

//home scss end

.footer {
    // height: 20vh;
    padding-left: 8.33333333%;

    .logo {
        max-width: 278px;
        overflow: hidden;
        height: 100%;
        width: auto;

        img {
            width: 100%;
        }
    }

    .top-btn {
        flex-direction: column;
        //display: flex;
        // justify-content: center;
        // align-items: center;
        @include center-content;

        svg {
            height: 5vw;
            min-height: 36px;
        }

        p {
            margin: 0.5rem 0;
            font-size: 1.5vw;
        }
    }
}

@mixin alignItems {
    display: flex;
    align-items: center;
    justify-content: space-between;
}

@mixin logoStyle {
    width: 100px;
    height: auto;
    padding-left: 10px;
}

/**Header*/

.top-header {
    height: 4.5rem;
    position: sticky;
    top: 0;
    background-color: white;
    padding: 0 1rem;
    display: flex;
    align-items: center;
    justify-content: space-between;
    z-index: 2;

    .content {
        font-size: 12px;
        font-weight: 200;
        padding: 0.5rem;
        gap: 0.5rem;
        @include alignItems()
    }

    .logo1 {
        @include logoStyle();
        content: url("./assets/images/logo_full.png");
    }

    .logo2 {
        @include logoStyle();
        content: url("./assets/images/logo_full.png");
    }


    @mixin icon {
        display: block;
        z-index: 2;
        background-color: #000;
        height: 3px;
        width: 16px;
    }

    .mobile-menu-button {

        .icon {
            @include icon;
            position: relative;


            &:before {
                content: '';
                position: absolute;
                top: -8px;
                @include icon;
            }

            &:after {
                content: '';
                top: 8px;
                position: absolute;
                @include icon;
            }
        }

        .clicked {
            transition-duration: 0.5s;
            background: transparent;

            &:before {

                transform: rotateZ(45deg) scale(1.4) translate(3.5px, 4px);
            }

            &:after {

                transform: rotateZ(-45deg) scale(1.4) translate(4px, -4.5px);
            }
        }


        .navigation-menu {
            transform: translateY(-100%);
            transition: transform 0.2s;
            position: fixed;
            top: 0;
            bottom: 0;
            left: 0;
            right: 0;


            .expanded {
                display: block;
                height: 100vh;
                background: #F8F8F8;
                width: 100%;
                position: absolute;
                transform: translateY(0);

                .mobile-link {
                    padding-block: 10%;
                    margin-top: 10%;
                }

                ul {
                    display: flex;
                    flex-direction: column;
                    height: 100%;
                    justify-content: center;
                }

                li {
                    text-align: center;
                    font-size: 48px;
                    padding: 36px 0;

                    a {
                        color: $red;
                        text-decoration: none;
                    }
                }

                .mobile-content {
                    display: flex;
                    flex-direction: column;
                    align-items: center;
                }
            }
        }
    }
}

/**Header**/


// Sidebar
.hide {
    display: none;
}

.left-header {
    background-color: #f8f8f8;
    height: 100%;
    position: fixed;
    z-index: 999;
    display: block;

    .navbar-left {
        display: flex;
        flex-direction: column;
        padding: 3rem 1rem;

        ul {

            li {
                writing-mode: vertical-rl;
                transform: rotate(180deg)
            }
        }

        a {
            padding: 3rem 0;
            font-weight: 600;
            font-size: 24px;
            text-decoration: none;
            color: #141414;
            font-family: "Outfit", sans-serif;

            &:hover {
                color: darkgray;
            }
        }
    }
}




.page-margin-wrapper {
    padding-left: 140px;
}

//meun page start
.meun-wrapper {

    .page-heading {
        position: absolute;

        h1 {
            font-weight: 500;
            font-size: 7vw;
        }
    }


    .page-header {
        margin-bottom: 80px;
        display: flex;
        align-items: center;
        position: relative;

        img {
            width: 100%;
            object-fit: cover;
            margin-left: 10rem;
            max-height: 7rem;
        }
    }

    .meun-table {
        display: flex;
        justify-content: space-between;
        padding-bottom: 7rem;

        .table-head {
            font-size: 20px;
            font-weight: 600;
            padding: 1rem;
        }

        .table-body {
            font-size: 16px;
            font-weight: 400;
            padding: 1rem;
        }

        .wide {
            max-width: 60%;
        }
    }



    @media only screen and (max-width: 768px) {
        .page-margin-wrapper {
            padding-left: 0;

            .page-header {
                img {
                    margin-left: 0;
                }
            }

            .page-heading {
                background-color: #D9D9D999;
                height: 100%;
                width: 100%;

                h1 {
                    position: absolute;
                    top: 50%;
                    left: 50%;
                    transform: translate(-50%, -50%);
                    font-size: 54px;
                }
            }

            .meun-table {
                flex-flow: row wrap;

                .drink-row {
                    margin-right: 40px;
                }

                .one {
                    flex: 1 1 auto;
                }

            }
        }

    }
}



//meun page scss end

//access page scss
.access {
    .main {
        display: flex;
        flex-direction: column;
        height: 100vh;
        width: 100%;
        // padding-top: 10%;
        padding-left: 8.33333333%;

        h3 {
            padding: 0;
            font-size: 4vw;
            margin: 0;
        }

        .ms-auto {
            width: auto;
            height: 100%;
            overflow: hidden;
            position: relative;
            margin: 0 2rem;
            max-height: 817px;

            svg {
                position: absolute;
                width: 100%;
                height: 100%;
                left: 6%;
                top: -10%;
            }
        }

        ul {
            list-style: none;
            padding: 0;
            margin: 0;
            width: 100%;
            display: flex;
            justify-content: space-between;

            li {
                width: 30%;

                a {
                    text-decoration: none;

                    &:hover {
                        color: darkgrey;
                    }
                }
            }
        }
    }
}

@media only screen and (max-width: 768px) {
    .access .main ul {
        display: flex;
        flex-direction: column;

        li {
            padding-block: 20px;
            width: auto;
        }
    }

}

//asscess scss end

.shop-wrapper {
    .shop-top-column {
        padding-top: calc(3rem + 3rem);
        // calc meun top paddinf + inner word padding
    }

    .awards img {
        padding-right: 1.5rem;
    }

    .coffee-card {
        @include center-content;
        width: calc(100%/4);
        height: 100%;
        flex-direction: column;
        gap: 10px;
        .image-wrapper{
            width: 200px;
            height: 200px;
            img{
                width: 100%;
                height: 100%;
                object-fit: cover;
            }
        }

        p{
            text-align: center;
        }
    }

    .product-row {
        display: flex;
        justify-content: space-around;
    }
    .image-wrap{
        position: relative;
        padding-bottom: 100%

        img {
            position: absolute;
            top: 50%;
            left: 50%;
            transform: translate(-50%, -50%);
            width: 100%;
            height: 100%;
            object-fit: cover;
        }
    }

    .quick-view {
        @include center-content;
        transition: all .3s ease-in-out;
        transform-origin: center;
        background-color: $bg-red;
        cursor: pointer;
        color: white;
        position: absolute;
        padding: 10px;
        bottom: 0;
        left: 50%;
        transform: translate(-50%, -50%);
        width: 50%;
        margin-bottom: 10px;
        // height: 38px;
        opacity: 0;
        z-index: 2;
        // scale: 0;
        &:hover {
            opacity: 1;
        }
    }
}

.popup-wrap {
position: fixed;
    top: 0;
    bottom: 0;
    left: 0;
    right: 0;
    transform: translateY(-100%);
    transition: transform 0.2s;

    .expanded {
        display: block;
        width: 100%;
        height: 100vh;
        position: fixed;
        top: 195px;
        background: hsl(0deg 0% 100% / 71%);
        transform: translateY(0);
    }
}

.popup {
    @include center-content;
}

.product {
    &:hover {
        .image-wrap img {
            scale: 1.02;
                // width: 105%;
                // height: 105%
        }
    }
}
.product-row .product {
    width: 25%;
    cursor: pointer;
}
.image-wrap {
    width: 100%;
    padding-bottom: 100%;
    position: relative;
}

.image-wrap img {
    position: absolute;
    width: 100%;
    height: 100%;
    top: 0;
    left: 0;
    transition: all 1s ease-in-out;
    transform-origin: center;
}

.content-container {
    position: relative;
    background-color: #fff;
    display: flex;
    flex-direction: column;
    align-items: center;
    padding: 4rem 7rem;

    &:hover {
        .quick-view {
            opacity: 1;
            scale: 1;

        }


    }

    

    img {
        min-width: 330px;
        transition: all 1s ease-in-out;
    }

    .heading {
        font-weight: 500;
        font-size: 25px;
        line-height: 32px;
        color: #464B49;
    }

    .subhead {
        font-size: 12px;
        line-height: 14px;
    }

    button {
        width: 143px;
        height: 38px;
        background: $bg-red;
        color: #fff;
        margin: 20px;
        border: none;
    }
}


